export function imgCompression (base64, quality, resultFun) {
    // let ready = new FileReader()
    // /*开始读取指定的Blob对象或File对象中的内容. 当读取操作完成时,readyState属性的值会成为DONE,如果设置了onloadend事件处理程序,则调用之.同时,result属性中将包含一个data: URL格式的字符串以表示所读取文件的内容.*/
    // ready.readAsDataURL(file)
    // ready.onload = function () {
    //
    // }
    canvasDataURL(base64, quality, resultFun)
  }
  function canvasDataURL (path, obj, callback) {
    var img = new Image()
    img.src = path
    img.onload = function () {
      var that = this
      // 默认按比例压缩
      let w = that.width, h = that.height, scale = w / h
      if (w > h) {
        w = obj.width ? (w < obj.width ? w : obj.width) : w
        h = w / scale
      } else {
        h = obj.height ? (h < obj.height ? h : obj.height) : h
        w = h * scale
      }
      var quality = 0.6 // 默认图片质量为0.7
      //生成canvas
      var canvas = document.createElement('canvas')
      var ctx = canvas.getContext('2d')
      // 创建属性节点
      var anw = document.createAttribute('width')
      anw.nodeValue = w
      var anh = document.createAttribute('height')
      anh.nodeValue = h
      canvas.setAttributeNode(anw)
      canvas.setAttributeNode(anh)
      ctx.drawImage(that, 0, 0, w, h)
      // 图像质量
      if(obj.quality && obj.quality <= 1 && obj.quality > 0){
        quality = obj.quality
      }
      // quality值越小，所绘制出的图像越模糊
      var base64 = canvas.toDataURL('image/jpeg', quality)
      // 回调函数返回base64的值
      // let blob = convertBase64UrlToBlob(base64)
      callback(base64)
    }
  }
  /**
   * 将以base64的图片url数据转换为Blob
   * @param urlData
   *            用url方式表示的base64图片数据
   */
  // DataURL转Blob对象
  export function dataURLToBlob (dataurl) {
    var arr = dataurl.split(',')
    var mime = arr[0].match(/:(.*?)/)[1]
    var bstr = atob(arr[1])
    var n = bstr.length
    var u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n)
    }
    return new Blob([u8arr], {type: mime})
  }
  export function dataUrlToFile (dataurl, filename) {
    let arr = dataurl.split(',')
    let mime = arr[0].match(/:(.*?);/)[1]
    let bstr = atob(arr[1])
    let n = bstr.length
    let u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n)
    }
    return new File([u8arr], filename, {type:mime})
  }
  // 图片文件压缩
  export function ImgfileResizetoFile(file, quality, maxWidth, maxHeight, fn) {
    function filetoDataURL (file,fn) {
        var reader = new FileReader()
        reader.onloadend = function (e) { 
            fn(e.target.result)
        }
        reader.readAsDataURL(file)
    }
    function dataURLtoImage(dataurl, fn) {
        var img = new Image()
        img.onload = function() {
            fn(img)
        }
        img.src = dataurl
    }
    function canvasResizetoFile(canvas, quality, fn) {
        canvas.toBlob(function(blob) {
            fn(blob)
        }, 'image/jpeg', quality)
    }
    function imagetoCanvas(image){
        var cvs = document.createElement("canvas")
        var ctx = cvs.getContext('2d')
        var sWidth = 1
        var sHeight = 1
        if (image.width > maxWidth || image.height > maxHeight) {
          sWidth = image.width/maxWidth
          sHeight = image.height/maxHeight
        }
        var s = sWidth > sHeight ? sWidth : sHeight
        cvs.width = image.width/s
        cvs.height = image.height/s
        ctx.drawImage(image, 0, 0, cvs.width, cvs.height)
        return cvs
    }
    filetoDataURL (file, function(dataurl) {
        dataURLtoImage(dataurl, function(image) {
            canvasResizetoFile(imagetoCanvas(image), quality, fn)
        })
    })
  }
  