<template>
  <div class="hello">
    <van-uploader :file-list="fileList" :after-read="afterRead"/>
    <p v-for="(wordsItem, i) in wordsObj" :key="'asdasd'+i">
      <template v-for="(item, index) in wordsItem.hanzi">
        <ruby :key="index" @click="read(item)"
          >{{ item }}<rt v-if="isChinese(item)">{{ wordsItem.pinyin[index] }}</rt></ruby
        >
      </template>
    </p>
  </div>
</template>

<script>
import axios from "axios";

import { btts, ttt } from "../baidu";
import { imgCompression } from "../utils";

var getPinyin = require("pinyin");
var han = /^[\u4e00-\u9fa5]+$/;
function isChinese(temp) {
  if (han.test(temp)) {
    return true;
  }
}

export default {
  name: "HelloWorld",
  data() {
    return {
      fileList: [
      ],
      hanzi: [],
      pinyin: [],
      str: "宝贝是个小流氓",
      tab: false,
      text: "我",
      per: [106, 110, 111, 103, 5, 1, 0, 3, 4],
      index: 0,
      voice: 0,
      postText: "",
      wordsObj:''
    };
  },
  props: {
    msg: String
  },
  mounted() {
    let str = this.fn('我的老家');
    console.log(str)
    this.wordsObj = str
    // this.str = '3.太阳病,或已发热,或未发热,必恶寒,体痛,呕逆,Thai Durong benh, hoac da phat nhiet hoac chura phat nhiet'
    // this.fn()
    // console.log(pinyin('khong truyen. Neu rat muon tho, neu tao phien, mach sac cap'))
    // axios.post('https://aip.baidubce.com/oauth/2.0/token',{
    //   grant_type:'client_credentials',
    //   client_id:'B24nezUbGPP5rVzG6ThQ8LO1',
    //   client_secret:'Fd94p0Ws0SMghL3owXmz4HxeOFKkqfxS'
    // }).then((res)=>{
    //   console.log(res)
    // })
    //dom加载完毕回调后注册按钮对象

    //  this.bttts()
    //      axios.post('https://aip.baidubce.com/rpc/2.0/nlp/v1/lexer?charset=UTF-8&access_token=24.b277fbcd3b5e17d6ecad50d687b21d3d.2592000.1578838446.282335-18017265',).then((res)=>{
    //   console.log(res)
    // })
    // this.ready(() => {
    //   this.playBtn = document.getElementById("playBtn");
    // });

    // 合成按钮

    // dom加载完毕回调
  },
  methods: {
    isChinese(temp) {
      if (han.test(temp)) {
        return true;
      }
    },
    afterRead(res){
      imgCompression(res.content, {height: 1000, width: 1000}, (base64) => {
      var imgs = base64.replace(/^data:image\/\w+;base64,/, "");
      this.uploadImage(imgs)
        // let filesObj = {}
        // filesObj['file'] = dataUrlToFile(base64, this.filename)
        // this.isOverSize = true
        // this.onReadSaveImage(filesObj)
      })
      // console.log(imgs)
      // console.log(res.content)
      // console.log(encodeURIComponent(res.content.replace('data:image/png;base64','')))
      // this.uploadImage(encodeURIComponent(res.content))
    },
    uploadImage(img){
      axios.post('https://nodeserver.hanabi90.usw1.kubesail.org/zidian',img).then((res)=>{
        // console.log('res',res.data)
        let resultArr=[]
        let wordsArr = res.data.words_result
        wordsArr.forEach((item)=>{
          let result = this.fn(item.words)
          resultArr.push(result)
        })
        this.wordsObj = resultArr
        
        // this.str = res.result;
        
      })
    },
    sendData() {
      let params = {
        text: this.str
      };
      axios.post("https://usw1.kubesail.com/", params).then(res => {
        console.log(res);
      });
    },
    tabvoice() {
      this.index++;
      this.index = this.index % this.per.length;
      this.voice = this.per[this.index];
    },
    palyall() {
      this.text = this.str;
      this.tts();
    },
    read(item) {
      this.text = item;
      this.tts();
    },
    ready(callback) {
      var doc = document;
      if (doc.addEventListener) {
        doc.addEventListener(
          "DOMContentLoaded",
          function() {
            callback();
          },
          false
        );
      } else if (doc.attachEvent) {
        doc.attachEvent("onreadystatechange", function() {
          if (doc.readyState === "complete") {
            callback();
          }
        });
      }
    },
    bttts() {
      // https://aip.baidubce.com/rpc/2.0/nlp/v1/lexer?charset=UTF-8&access_token=24.b277fbcd3b5e17d6ecad50d687b21d3d.2592000.1578838446.282335-18017265
      ttt({
        charset: "UTF-8",
        access_token:
          "24.23bfab29758df833b59e95c9aab202b4.2592000.1603988305.282335-15670276",
        text: "百度是一家高科技公司"
      });
    },
    tts() {
      // 调用语音合成接口
      // 参数含义请参考 https://ai.baidu.com/docs#/TTS-API/41ac79a6
      // eslint-disable-next-line no-unused-vars
      var that = this;
      this.audio = btts(
        {
          tex: this.text,
          tok:
            "24.23bfab29758df833b59e95c9aab202b4.2592000.1603988305.282335-15670276",
          spd: 3,
          pit: 5,
          vol: 15,
          per: this.voice
        },
        {
          volume: 0.3,
          autoDestory: true,
          timeout: 10000,
          hidden: true,
          onSuccess: htmlAudioElement => {
            // console.log(htmlAudioElement)
            that.audio = htmlAudioElement;
            this.audio.play();
          },
          // onError: (text)=> {
          //     // console.log(text)
          // },
          onTimeout: () => {
            // console.log('timeout')
          }
        }
      );
    },
    // 暂停按钮
    pause() {
      if (this.audio === null) {
        console.log("请先点击合成");
      } else {
        this.audio.pause();
      }
    },

    // 取消按钮
    cancel() {
      if (this.audio === null) {
        alert("请先点击合成");
      } else {
        this.audio.pause();
        document.body.removeChild(this.audio);
        this.audio = null;
        this.playBtn.innerText = "准备中";
      }
    },
    check() {
      console.log(this.$refs.iframe.document.getElementById("content"));
    },
    uploadfile() {
      let reads = new FileReader();
      var file = document.getElementById("file").files[0];
      reads.readAsText(file, "utf-8");
      // console.log(reads);
      var that = this;
      reads.onload = function(e) {
        // document.getElementById('result').innerText = e.target.result
        that.str = e.target.result;
        that.fn();
      };
      // reads.onloadstart = function(e) {
      //     console.log('onloadstart ---> ', e)
      // }
      // reads.onloadend = function(e) {
      //     console.log('onloadend ---> ', e)
      // }
      // reads.onprogress = function(e) {
      //     console.log('onprogress ---> ', e)
      // }
    },
    fn(str) {
      // var arr =[]
      // var str = this.str;
      // arr = Array.from(str)
      str = str.replace(/\s*/g, "");
      var arr2 = [];
      var arr3 = [];
      for (let index = 0; index < str.length; index++) {
        const item = str[index];
        if (isChinese(item)) {
          arr2.push(item);
        } else {
          arr3.push({ index, item });
        }
      }
      arr2 = arr2.join("");
      // console.log(arr3)
      var hanzi = Array.from(arr2);
      var pinyin = getPinyin(arr2);
      // console.log(this.hanzi)
      for (let i = 0; i < arr3.length; i++) {
        const item = arr3[i];
        hanzi.splice(item.index, 0, item.item);
        pinyin.splice(item.index, 0, item.item);
      }
      return {
        hanzi,
        pinyin
      }
    }
  }
};
</script>

